<template>
  <div>
    <h1>{{ msg }}</h1>

    <form @submit.prevent="handleAddTask">
      <input type="text" v-model="task" placeholder="To Do List" />
      <button type="submit">Add</button>
    </form>

    <ul>
      <li v-for="(task, index) in tasks" :key="index">
        {{ task }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "ToDo",

  props: {
    msg: String
  },

  data () {
    return {
      task: '',
      tasks: []
    }
  },

  methods: {
    handleAddTask () {
      if (this.task.trim()) {
        this.tasks.push(this.task);
        this.task = '';
      }
    }
  }
}
</script>

